.click-area {
  width: 400px;
  height: 400px;
  border-radius: 100%;
  background: orange;
  margin: 100px auto 0;
  z-index: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-box-shadow: 0px 6px 17px 0px rgba(0,0,0,0.53);
          box-shadow: 0px 6px 17px 0px rgba(0,0,0,0.53);
}

.click-area:hover {
  cursor: pointer;
}



